.header{
    width: 100%;
    position: fixed;
    top:0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav{
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo{
    border-radius: 0 8px 0 9px;
    transform:rotate(-5deg);
    font-size: large;
}
.Clogo{
    font-size:1.4rem;
}

.nav__logo, .nav__toggle{
    color:var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list{
    display: flex;
    column-gap: 1.5rem;
}

.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .1s;
    text-transform: uppercase;
}

.nav__close, .nav__icon, .nav__toggle{
    display: none;
}

.active__link, .nav__link:hover{
    color: var(--title-color-dark);
    transform: scale(1.1);
}

/* change background-header */

.scroll-header{
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
}




/* Responsive breakpoint */

  @media screen and (max-width:768px) {
        .header{
            top:initial;
            bottom: 0;
        }

        .nav{
            height: var(--header-height);
        }

        .nav__menu{
            position: fixed;
            bottom:-100%;
            left: 0;
            width: 100%;
            background-color: var(--body-color);
            padding: 2rem 1.5rem 4rem;
            box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
            border-radius: 1.5rem 1.5rem 0 0;
            transition: .2s;
        }

        /* Show Menu */

        .show__menu{
            bottom: 0;
        }


        .nav__link{
            text-transform: capitalize;
        }
        .nav__list{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
        }

        .nav__icon{
            font-size: 1.2rem;
        }

        .nav__close{
            position: absolute;
            right: 1.3rem;
            bottom: .5rem;
            font-size: 2rem;
            cursor: pointer;
            color: var(--title-color);
        }

        .nav__close:hover{
            color: var(--title-color-dark);
        }
/* 
        .nav__logo{
            display: none;
        } */


        .nav__toggle{
            font-size: 1.5rem;
            cursor: pointer;
        }
        .nav__close, .nav__icon, .nav__toggle{
            display: block;
            
        }
    }


       @media  screen and (max-width:350px) {
            .nav__menu{
                padding: 2rem 0.25rem 4rem;
            }

            .nav__list{
                column-gap: 0;
            }
    }
